@charset "UTF-8";

// init:
@import '../config/variables';
@import '../config/bootstrap-variables';
@import '../config/bootstrap';
@import '../config/typography';
@import 'mixins';
@import 'helpers';

// Don't edit the following sections, they should always be preceded by a blank
// line and have exactly those names for the `$ yo toolbox:generate` command
// to work

//
// atoms:
@import 'atoms/button/button';
@import 'atoms/icon/icon';
@import 'atoms/badge/badge';
@import 'atoms/flag/flag';

//
// molecules:
@import 'molecules/nav/nav';
@import 'molecules/list-item/list-item';
@import 'molecules/step-nav/step-nav';
@import 'molecules/form-control/form-control';
@import 'molecules/card/card';

//
// organisms:

////////////
.styleguide {
  grid-template-columns: initial !important;
}

.tlbx-sidebar-wrapper {
  display: none;
}

.tlbx-content-wrapper {
  grid-column: 1 !important;
  margin-left: 0 !important;
  padding: 15px;
}

.tlbx-item-code {
  display: none;
  max-width: 100vw;

  pre code {
    white-space: pre-wrap;
  }
}
