@charset 'utf-8';

.list-items {
  @include list-unstyled();
}

// Single list item
.list-item {
  display: flex;
  align-items: center;
  margin-bottom: $spacer;
  background-image: linear-gradient(to right, rgba($brand-secondary, 0) 5%, rgba($brand-secondary, 0.08) 50%);
  border-radius: $btn-border-radius;

  @include transition(transform 0.2s ease);

  @include hover-focus {
    text-decoration: none;
  }

  @include media-breakpoint-down(sm) {
    flex-wrap: wrap;
  }

  &:active,
  &.active {
    transform: scale(0.98);
    background-image: linear-gradient(to right, rgba($brand-secondary, 0.02) 5%, rgba($brand-secondary, 0.3) 70%);
  }

  .btn:first-of-type {
    border: 0;
  }
}

// Left icon has a right margin
.list-item-icon {
  margin-right: $spacer;

  @include icon-size(30px);
}

// The title pushes everything to the right
.list-item-title {
  margin-right: auto;
  font-size: $font-size-lg;

  @include media-breakpoint-down(sm) {
    flex: 1 1 100%;
  }
}

// Text inside list item is black and has a right margin
.list-item-inside {
  color: $body-color;
  margin-right: $spacer * 3;
  white-space: nowrap;

  @include media-breakpoint-down(sm) {
    margin-right: $spacer;
  }
}

// Display a flag when the status of the list item is set
.list-item-flag {
  margin: ($spacer * -2) 0;
  color: $inverse-color;

  svg {
    fill: $blue;
  }


  @include flag-size(50px);
}
