@charset "utf-8";

// Handle shadows on elements, with brand-primary color
.shadow-to-right {
  @include drop-shadow(bottom);

  @include media-breakpoint-up(md) {
    @include drop-shadow(right);
  }
}

.shadow-to-left {
  @include drop-shadow(bottom);

  @include media-breakpoint-up(make-col-modifier) {
    @include drop-shadow(left);
  }
}

.shadow-to-bottom {
  @include drop-shadow(bottom);
}

.shadow-floating {
  @include floating-shadow();
}

// Background helpers
@include bg-variant('.bg-white', $white);
@include bg-variant('.bg-blue', $blue);
@include bg-variant('.bg-primary', $purple-light);

.bg-blue,
.bg-primary {
  color: $inverse-color;

  .text-muted {
    color: rgba($inverse-color, 0.75) !important;
  }
}
